<template>
  <div class="pie">
    <div class="top">
      <h3>出块贡献</h3>
      <div class="pies" v-if="list !== ''">
        <div id="pie" style="height: 350px"></div>
        <div class="bck"></div>
        <div class="bck2">
          <div class="circle"></div>
          <div class="circle2"></div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <h3>区块浏览</h3>
      <div class="pillar">
        <div id="pillar" style="height: 450px"></div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import axios from "axios";

export default {
  data() {
    return {
      list: [],
      myChart: {},
      myChart2: {},
      data: [],
      datax: [],
      datay: [],
      datay2: [],
      show: false,
    };
  },
  methods: {
    pie() {
      axios.get("/api/pjData/contributionBlock").then((res) => {
        let list = res.data.data;
        console.log(res);
        list.forEach((item) => {
          this.list.push({
            value: Number(item.score.replace("%", "") * 100),
            name: item.nodeName,
          });
          this.data.push({
            icon: "rect",
            name: item.nodeName,
          });
        });
        this.show = true;
        this.myChart = this.$echarts.init(document.getElementById("pie"));
        this.option = {
          tooltip: {
            trigger: "item",
          },
          legend: {
            height: "60%",
            orient: "vertical",
            itemWidth: 10,
            itemHeight: 10,
            itemGap: 50,
            top: "25%",
            left: "16%",
            textStyle: {
              color: "#fff",
              rich: {
                a: {
                  width: 55,
                  fontSize: 20,
                  fontWeight: 200,
                },
              },
            },
            formatter: ["{a|{name}}"].join("\n"),
            data: this.data,
          },
          series: [
            {
              name: "",
              type: "pie",
              left: "50%",
              radius: ["40%", "70%"],
              avoidLabelOverlap: false,
              label: {
                show: false,
                formatter: "{d}%", //自定义显示格式(b:name, c:value, d:百分比)
              },
              itemStyle: {
                normal: {
                  borderWidth: 15, // 间距的宽度
                  borderColor: "rgb(3, 24, 53)", //背景色
                },
              },
              data: this.list,
            },
          ],
        };
        this.myChart.setOption(this.option, true);
      });
    },
    pillar() {
      axios.get("/api/pjData/numberAndSizeBlock").then((res) => {
        console.log(res.data.data);
        let list = res.data.data;
        list.forEach((item) => {
          this.datax.push("块" + item.heightHash);
          this.datay.push(item.number);
          this.datay2.push(parseInt(item.size));
        });
        console.log(this.datax);
        console.log(this.datay);
        console.log(this.datay2);
        this.myChart2 = this.$echarts.init(document.getElementById("pillar"));
        const colors = ["rgb(133,144,90)", "rgb(27,143,146)"];
        this.option2 = {
          color: colors,
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
            },
          },
          grid: {
            right: "20%",
          },
          toolbox: {
            feature: {
              dataView: { show: true, readOnly: false },
              restore: { show: true },
              saveAsImage: { show: true },
            },
          },
          legend: {
            data: ["出块数据量", "块大小"],
            textStyle: {
              color: "#fff",
            },
          },
          xAxis: [
            {
              type: "category",
              axisTick: {
                alignWithLabel: true,
              },
              axisLabel: {
                //  X 坐标轴标签相关设置
                interval: 0,
                rotate: "45",
                fontSize: 16,
              },
              // prettier-ignore
              data: this.datax,
            },
          ],
          yAxis: [
            {
              type: "value",
              name: "块大小",
             
              min: 0,
              max: 70,
              interval: 14,
              position: "right",
              alignTicks: true,
              axisLine: {
                show: true,
                lineStyle: {
                  color: "#fff",
                },
              },
              axisLabel: {
                formatter: "{value}",
              },
            },
            {
              type: "value",
              min: 0,
              max: 10,
              interval: 2,
              name: "出块数据量",
              position: "left",
              alignTicks: true,
              axisLine: {
                show: true,
                lineStyle: {
                  color: "#fff",
                },
              },
              axisLabel: {
                formatter: "{value}",
              },
            },
          ],
          series: [
            {
              name: "块大小",
              type: "line",
              symbol:'none',
              yAxisIndex: 0,
              data: this.datay2,
            },
            {
              name: "出块数据量",
              type: "bar",
              yAxisIndex: 1,
              itemStyle: {
                color: {
                  type: "linear",
                  x: 1, // 右
                  y: 0, // 下
                  x2: 0, // 左
                  y2: 0, // 上
                  colorStops: [
                    {
                      offset: 0,
                      color: "rgb(31,161,160)", // 0% 处的颜色
                    },
                    {
                      offset: 0.9,
                      color: "rgb(13,71,90)", // 90% 处的颜色
                    },
                  ],
                },
              },

              data: this.datay,
            },
          ],
        };
        this.myChart2.setOption(this.option2, true);
      });
    },
  },
  mounted() {
    this.pie();
    this.pillar();
  },
};
</script>

<style scoped>
.pie {
  width: 1000px;
  height: 1000px;
  background: rgb(3, 24, 53);
  margin: 100px auto;
}
h3 {
  font-size: 30px;
  color: rgb(138, 196, 204);
  text-align: left;
  margin: 0 0 0 30px;
  box-sizing: border-box;
  padding-top: 30px;
}
.pies {
  width: 100%;
  height: 350px;
  position: relative;
}
.bck {
  width: 125px;
  height: 125px;
  border: 1px solid rgb(71, 130, 142);
  border-radius: 100%;
  position: absolute;
  top: 105.5px;
  right: 185px;
  z-index: 999;
}
.bck2 {
  width: 270px;
  height: 270px;
  border: 1px dashed rgb(71, 130, 142);
  border-radius: 100%;
  position: absolute;
  top: 40px;
  right: 115px;
}
.bck2 .circle {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: rgb(190, 253, 255);
  position: absolute;
  top: -5px;
  left: 49%;
}
.bck2 .circle2 {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: rgb(190, 253, 255);
  position: absolute;
  bottom: -5px;
  left: 49%;
}
#pie {
  z-index: 999;
}
</style>